				<h3>2 - Quelles ont √©t√© les actions citoyennes qui ont pris corps dans l‚Äôespace public{{'\xa0'}}?</h3>
					<p>En ce qui concerne les actions citoyennes, la Ville de Montr√©al mentionnait, le 9{{'\xa0'}}avril 2020, dans ses communiqu√©s de presse qu‚Äôelle offrait un programme de financement en ce temps de crise pour les petites entreprises montr√©alaises et les organismes √† but non lucratif (OBNL) <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32550&amp;ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_communiques/communiques">R√©f{{'\xa0'}}&#x1f855;</a>. Dans Montr√©al-Nord, un des secteurs les plus touch√©s par la COVID-19 et des plus d√©favoris√©s de Montr√©al, le <i>Journal M√©tro</i>, le 29{{'\xa0'}}avril 2020, annon√ßait la mise en ≈ìuvre d‚Äôun syst√®me de distribution de masques cr√©√© par les citoyens <a class="reference-link" target="_blank" href="https://journalmetro.com/local/montreal-nord/2442201/masques-gratuits-et-corridors-sanitaires-a-montreal-nord/?fbclid=IwAR0MWd8tPgINnPck2iZOnoB9vuVM1gf1mGpWXv-mdPPLfjhDmPblBsa9fhU">R√©f{{'\xa0'}}&#x1f855;</a>. Le 3 mai 2020, la municipalit√© d√©clarait que l‚Äôeffort collectif avait permis de distribuer gratuitement 20{{'\xa0'}}800 masques dans Montr√©al-Nord <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32614">R√©f{{'\xa0'}}&#x1f855;</a>. Dans l‚Äôarrondissement Villeray-Saint-Michel-Parc-Extension, le gouvernement municipal pr√©sentait dans un communiqu√© de presse datant du 4{{'\xa0'}}mai 2020, mise sur pied d‚Äôun syst√®me de livraison √† v√©lo en collaboration avec les citoyens pour encourager les commer√ßants locaux <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32618&amp;ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_comm