					<p><i>La Gazette</i> rappelait le 14{{'\xa0'}}avril 2020 qu‚Äôau printemps, √† l‚ÄôOuest de Montr√©al, l‚Äôarrondissement Pierrefonds-Roxboro avait souvent √©t√© victime d'inondations saisonni√®res. Ainsi, la Gazette rapportait que de concert avec les autorit√©s locales, les citoyens agissaient avec pr√©vention. Ils distribuaient des sacs de sable aux r√©sidents susceptibles d‚Äô√™tre impact√©s par la crue des eaux et mettaient en place des barrages en plus des pompes d‚Äô√©gout ext√©rieures afin de pr√©server leur maison contre les inondations <a class="reference-link" target="_blank" href="https://montrealgazette.com/news/local-news/west-island-gazette/west-island-officials-and-homeowners-brace-for-spring-floods/">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Sur une note plus positive, <i>Le Journal de Montr√©al</i> indiquait, le 7{{'\xa0'}}mai 2020, que pour √©gayer les ruelles de Mercier‚ÄìHochelaga-Maisonneuve, le collectif <i>Bonheur Mobile</i>, regroupant des artistes du cirque sans contrats en raison du confinement, organisait des d√©fil√©s dans les ruelles <a class="reference-link" target="_blank" href="https://www.journaldemontreal.com/2020/05/07/hochelaga-maisonneuve--bonheur-mobile-pour-egayer-les-ruelles">R√©f{{'\xa0'}}&#x1f855;</a>. En d√©but de confinement, <i>Le Devoir</i> informait, le 7{{'\xa0'}}avril 2020, que le collectif <a class="reference-link" target="_blank" href="https://www.paperole.com/"><i>Paperole</i></a> r√©unissait un groupe cr√©atif pour r√©aliser des murales d‚Äôaffiches color√©es √† travers les rues en vue de revaloriser la ville devenue d√©serte et de remercier les travailleurs essentiels <a class="reference-link" target="_blank" href="https://www.ledevoir.com/culture/arts-visuels/576539/merci-sur-tous-les-murs-de-la-ville">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p><i>Radio-Canada</i> affirmait, le 10{{'\xa0'}}avril 2020, dans un √©ditorial sur l‚Äôarc-en-ciel que Gabriella Cucinelli, Italienne et employ√©e dans un centre de la petite enfance (CPE), avait entam√© au Qu√©bec le mouvement de l‚Äôarc-en-ciel avec le slogan <i>√áa va bien aller</i> <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1692655/arc-en-ciel-ca-va-bien-aller-histoire-mythologie-communaute-gaie">R√©f{{'\xa0'}}&#x1f855;</a><i>.</i> Dans le m√™me article, Mme. Cucinelli avouait qu‚Äôau d√©part elle avait le d√©sir de montrer sa solidarit√© au peuple italien, cependant rapidement l‚Äôarc-en-ciel √©tait devenu un signe de r√©confort pr√©sent sur l‚Äôensemble du territoire qu√©b√©cois <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1692655/arc-en-ciel-ca-va-bien-aller-histoire-mythologie-communaute-gaie">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p class="conclusion">Les actions citoyennes rep√©r√©es dans les informations recueillies, telles que la distribution de masques, de denr√©es alimentaires, la livraison √† v√©lo et les mesures pr√©ventives face aux inondations printani√®res montr√©alaises, ont √©t√© d√©ploy√©es dans les diverses communaut√©s de Montr√©al. Ces actions citoyennes qui ont pris corps dans l‚Äôespace public ont √©t√© diversifi√©es en fonctions des besoins des communaut√©s et populations vari√©es de Montr√©al. Les OBNL qui ont adapt√© et poursuivi leurs activit√©s ont jou√© un r√¥le important dans l‚Äôaide aux communaut√©s vuln√©rables de Montr√©al (<i>Architecture Sans Fronti√®res Qu√©bec, Cactus, Organisation des Jeunes de Parc-Extension, Ressources action-alimentation)</i>. Toutefois, en regard des informations analys√©es, les OBNL venant en aide aux itin√©rants pr√©sentaient des difficult√©s √† poursuivre leur offre de service en raison du manque de mat√©riel et de la distanciation physique difficile √† respecter. Au-del√† des difficult√©s rapport√©es, il est int√©ressant de noter des actions citoyennes associ√©es au mouvement <i>√áa va bien aller</i> (affichage d‚Äôarcs-en-ciel aux vitres des habitations), des projets de designers graphiques, telles les affiches de <i>Paperole </i>et des projets de saltimbanques, comme le cirque<i> Bonheur Mobile, </i>qui r√©v√®lent l‚Äôimportance de cr√©er un sentiment de solidarit√© collective, de divertir la population et d‚Äô√©gayer l‚Äôespace public montr√©alais durant le confinement.</p>
				<h3>3 - Concernant les espaces de vie priv√©e ou publique, quelles ont √©t√© les mesures engag√©es pour satisfaire les besoins de la population{{'\xa0'}}?</h3>
					<p>Le 17{{'\xa0'}}mars 2020, au d√©but de la crise sanitaire, <i>Radio-Canada</i>, annon√ßait que les dortoirs pour les sans-abri g√©r√©s par des organismes √† but non lucratif (OBNL) h√©sitaient √† fermer de peur que la situation s'envenime. Toutefois, ce m√™me article rapportait que les OBNL d√©ployaient des mesures d'hygi√®ne en offrant des masques pour permettre √† la client√®le et aux b√©n√©voles de se prot√©ger <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1671967/montreal-covid-19-sans-abri-vulnerable-salubrite-desinfection-prevention">R√©f{{'\xa0'}}&#x1f855;</a>. Cet article signalait que du mat√©riel sanitaire, tel des produits nettoyants, d√©sinfectants et des lingettes √©taient mis √† la disposition des organismes qui viennent en aide aux itin√©rants <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1678832/coronavirus-itinerants-montreal-aide-sans-abri-isolement-covid-19">R√©f{{'\xa0'}}&#x1f855;</a>. <i>Radio-Canada</i>, le 8{{'\xa0'}}avril 2020, partageait que la Ville de Montr√©al r√©quisitionnait quatre h√¥tels vides pour h√©berger ces personnes et leur offrir un milieu de vie r√©pondant √† leurs besoins <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1691992/hotels-sans-abris-itinerants-covid-toronto-montreal">R√©f{{'\xa0'}}&#x1f855;</a>. Dans cet article, une source √† l‚Äôh√¥tel de ville de Montr√©al d√©clarait m√™me que{{'\xa0'}}: ¬´{{'\xa0'}}bient√¥t, plus personne n'aura besoin de dormir dans la rue. √áa sera du jamais vu{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1691992/hotels-sans-abris-itinerants-covid-toronto-montreal">R√©f{{'\xa0'}}&#x1f855;</a>. Selon <i>Radio-Canada</i>, le 15{{'\xa0'}}avril 2020, la Ville de Montr√©al demandait √† la Biblioth√®que des Archives nationales du Qu√©bec (BAnQ) de se transformer en halte-r√©pit, bien que celle-ci soit ferm√©e au grand public, car il s‚Äôagit d‚Äôun rep√®re r√©confortant pour les itin√©rants <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694077/montreal-haltes-ouverture-personnes-sans-abri-itinerant-coronavirus">R√©f{{'\xa0'}}&#x1f855;</a>. Dans ce m√™me article, Jean-Louis Roy, pr√©sident-directeur g√©n√©ral de BAnQ, divulguait que{{'\xa0'}}: ¬´{{'\xa0'}}de 200 √† 250 personnes sans domicile fixe avaient l'habitude de fr√©quenter quotidiennement la biblioth√®que avant la crise. Elles venaient s'y informer, effectuer des recherches d'emploi et se d√©tendre.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694077/montreal-haltes-ouverture-personnes-sans-abri-itinerant-coronavirus">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Un communiqu√© de presse du gouvernement du Qu√©bec soulevait, le 15{{'\xa0'}}avril 2020, que les lourdes contraventions concernant la distanciation distribu√©es par le Service de Police de la Ville de Montr√©al (SPVM) touchaient de mani√®re disproportionn√©e les plus d√©munis, les personnes racis√©es et les communaut√©s marginalis√©es, qui sont aussi le plus souvent victime de profilage <a class="reference-link" target="_blank" href="http://www.fil-information.gouv.qc.ca/Pages/Article.aspx?idArticle=2804150115">R√©f{{'\xa0'}}&#x1f855;</a>. De ce fait, le gouvernement du Qu√©bec annon√ßait que <i>La Commission des droits de la personne et des droits de la jeunesse du Qu√©bec </i>d√©non√ßait les contraventions donn√©es √† des jeunes en situation d'itin√©rance et l'impact discriminatoire de l'interdiction de rassemblement sur les personnes en situation d'itin√©rance <a class="reference-link" target="_blank" href="http://www.fil-information.gouv.qc.ca/Pages/Article.aspx?idArticle=2804150115">R√©f{{'\xa0'}}&#x1f855;</a>. Dans ce m√™me communiqu√©, la Commission rappelait que{{'\xa0'}}: ¬´{{'\xa0'}}le profilage discriminatoire demeure interdit m√™me lorsque des mesures d'urgence sanitaire et d'urgence civile sont en place. Celles-ci ne peuvent porter atteinte, directement ou indirectement, au droit √† l'√©galit√©.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="http://www.fil-information.gouv.qc.ca/Pages/Article.aspx?idArticle=2804150115">R√©f{{'\xa0'}}&#x1f855;</a>. De plus, <i>La Presse</i> attestait, le 19 avril 2020, que <a class="reference-link" target="_blank" href="https://ccla.org/our_mission/?lang=fr"><i>L'Association canadienne des libert√©s civiles</i></a> (ACLC) effectuait une demande d‚Äôamnistie sur toutes les contraventions distribu√©es pour raison de distanciation <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/202004/19/01-5270027-covid-19-lamnistie-sera-reclamee-pour-les-contraventions.php">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>En ce qui a trait aux espaces de vie priv√©e, le gouvernement du Qu√©bec d√©clarait l‚Äôouverture des chantiers de construction en date 20{{'\xa0'}}avril 2020 pour √©viter que des propri√©taires ne puissent pas acc√©der √† leur logement lors des d√©m√©nagements habituels du 1<sup>er</sup>{{'\xa0'}}juillet <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1693975/coronavirus-construction-chantiers-apchq-entrevue">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>La Ville de Montr√©al annon√ßait, le 29{{'\xa0'}}avril 2020, dans un communiqu√© de presse les nombreuses mesures qu‚Äôelle mettait en place pour venir en aide √† l‚Äôarrondissement de Montr√©al-Nord, fortement affect√© par la crise sanitaire (distribution de 2{{'\xa0'}}000 masques non r√©utilisables et de 5{{'\xa0'}}000 masques r√©utilisables, d√©ploiement d'une nouvelle campagne d'affichage dans les lieux les plus achaland√©s de l'arrondissement, am√©nagement de corridors sanitaires suppl√©mentaires) <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32602">R√©f{{'\xa0'}}&#x1f855;</a>. La mairesse Christine Black confiait qu‚Äô: ¬´{{'\xa0'}}en plus des nombreux efforts d√©ploy√©s depuis le jour 1 de la crise, en concertation avec nos partenaires sur le terrain, nous allons tout faire pour assurer la s√©curit√© physique et psychologique de nos citoyennes et citoyens{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32602">R√©f{{'\xa0'}}&#x1f855;</a>. En amont, <i>La Presse</i> r√©pertoriait, le 12 mai 2020, qu‚Äôune clinique mobile de d√©pistage de la COVID-19 circulerait via un autobus de la <i>Soci√©t√© de transport de Montr√©al</i> (STM) dans l‚Äôarrondissement de Montr√©al-Nord <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-05-12/montreal-nord-un-beau-decor">R√©f{{'\xa0'}}&#x1f855;</a>. Cependant, Patrick Lagac√©, dans cette m√™me chronique, soulignait que la Ville de Montr√©al ne diffusait pas efficacement les d√©placements de sa clinique mobile chez les communaut√©s de Montr√©al-Nord <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/202005/12/01-5273173-montreal-nord-un-beau-decor.php">R√©f{{'\xa0'}}&#x1f855;</a>. Le 14 mai 2020, la Ville de Montr√©al indiquait dans un communiqu√© de presse que la m√™me initiative serait mise en ≈ìuvre dans l‚Äôarrondissement Mercier‚ÄìHochelaga-Maisonneuve <a class="reference-link" target="_blank" href="https://www.newswire.ca/fr/news-releases/mhm-est-present-sur-le-terrain-covid-19-intensification-des-mesures-871780212.html">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Dans <i>Le Devoir</i>, en date du 29{{'\xa0'}}avril 2020, la directrice r√©gionale de la sant√© publique de Montr√©al, la D<sup>re</sup> Myl√®ne Drouin analysait la situation √† Montr√©al-Nord en mentionnant qu‚Äô{{'\xa0'}}: ¬´{{'\xa0'}}il y a des enjeux sur la capacit√© de maintenir la distanciation sociale. Il y a une densit√© urbaine importante. Il y a moins de parcs. On nous a aussi dit que dans certains commerces, c‚Äô√©tait difficile de respecter les mesures de distanciation{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.ledevoir.com/politique/montreal/577900/montreal-norden-zone-rouge">R√©f{{'\xa0'}}&#x1f855;</a>. Le 30{{'\xa0'}}avril 2020, Nicolas Zorn, directeur de l‚ÄôObservatoire qu√©b√©cois des in√©galit√©s (OQI), confiait au <i>Devoir </i>que{{'\xa0'}}: ¬´{{'\xa0'}}les personnes au statut pr√©caire sont plus expos√©es. Les femmes et les personnes racis√©es sont surrepr√©sent√©es dans cette situation{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/577984/aux-confins-des-inegalites">R√©f{{'\xa0'}}&#x1f855;</a>. En appui √† cette citation, le <i>Journal de Montr√©al</i>, le 5 mai 2020, rapportait que{{'\xa0'}}:{{'\xa0'}}¬´{{'\xa0'}}selon la m√©decin en chef de la Ville (Toronto), la Dre Eileen de Villa, le taux de propagation du coronavirus et le taux d'hospitalisation caus√© par la maladie sont disproportionnellement plus √©lev√©s dans les quartiers o√π le revenu par m√©nage est moins √©lev√©. Les quartiers comptant la plus forte proportion de nouveaux arrivants sont aussi plus touch√©s.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.journaldemontreal.com/2020/05/05/la-covid-19-fait-plus-de-ravages-dans-les-quartiers-a-faible-revenu">R√©f{{'\xa0'}}&#x1f855;</a>. Ce m√™me article indiquait que la sant√© publique de Montr√©al d√©clarait que l'arrondissement de Montr√©al-Nord, o√π la population est principalement immigrante et d√©munie, √©tait l'un des plus touch√©s par la pand√©mie <a class="reference-link" target="_blank" href="https://www.journaldemontreal.com/2020/05/05/la-covid-19-fait-plus-de-ravages-dans-les-quartiers-a-faible-revenu">R√©f{{'\xa0'}}&#x1f855;</a>. En d√©finitive, <i>La Presse</i> d√©clarait, le 9 mai 2020, que le confinement est v√©cu de mani√®re in√©gale consid√©rant que la population r√©side dans des logements de taille et qualit√© diff√©rentes selon les arrondissements <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/ff83c3e0-b983-42ae-9d85-f2de0fbcd024__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. Cette m√™me source r√©v√©lait que le confinement ne peut √™tre pratiqu√© dans le confort par tous <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/ff83c3e0-b983-42ae-9d85-f2de0fbcd024__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.{{'\xa0'}}</p>
					<p class="conclusion">Les espaces de vie priv√©e et publique varient grandement en fonction de la population diversifi√©e de Montr√©al. En effet, les communaut√©s plus d√©munies et racis√©es ont √©t√© les plus vuln√©rables face √† la crise comme le d√©montrent les articles relev√©s pr√©c√©demment. √Ä titre d‚Äôexemple, l‚Äôarrondissement de Montr√©al-Nord a √©t√© fortement touch√© par la crise sanitaire. Les efforts soutenus de la Ville de Montr√©al ont √©t√© d√©ploy√©s pour aider cette population en difficult√©, comme la distribution de masques et l‚Äôaugmentation de corridors sanitaires. Toutefois, il a √©t√© mentionn√© que l‚Äôune des populations les plus vuln√©rables au confinement demeure les itin√©rants. En effet, leur espace de vie √©tant majoritairement public, ils se sont heurt√©s √† de nombreuses difficult√©s de vivre leur quotidien (diminution des services offerts par les OBNL leur venant en aide). Cependant, il est int√©ressant de constater que certains √©difices publics ont permis leur accueil, telle la biblioth√®que <i>BAnQ</i>. De plus, il est √† noter, en regard des informations recueillies, que ces itin√©rants se sont fait appr√©hender par les autorit√©s publiques en raison du non-respect du confinement et de la distanciation. L‚Äôimpossibilit√© de respecter les mesures sanitaires et de se loger constituait les principales causes de cette situation. Sur cet aspect, le confinement a √©t√© v√©cu de mani√®re diff√©renci√©e par la population montr√©alaise en fonction de leur lieu de r√©sidence.</p>
				<h3>4 - La p√©riode de confinement a-t-elle engendr√© une valorisation du paysage urbain aupr√®s des Montr√©alais{{'\xa0'}}?</h3>
					<p>Le confinement incitait <i>Le Devoir</i>, un journal montr√©alais, √† se questionner sur l‚Äôavenir et l‚Äôusage des espaces publics de Montr√©al pour l‚Äô√©t√© 2020. De ce fait, <i>Le Devoir</i> indiquait, le 29{{'\xa0'}}avril 2020, que la Ville de Montr√©al pr√©parait un plan d‚Äôam√©nagement qui selon elle permettait aux citadins de profiter de leur paysage urbain estival tout en respectant les mesures sanitaires <a class="reference-link" target="_blank" href="https://www.ledevoir.com/politique/montreal/577896/un-ete-a-reinventer">R√©f{{'\xa0'}}&#x1f855;</a>. De plus, dans un communiqu√© de presse du 11 mai 2020, la mairesse de la Ville de Montr√©al, Val√©rie Plante, divulguait son intention face √† un plan de mobilit√© estivale encourageant la pi√©tonnisation de plusieurs rues et qui permettait aux citadins de ¬´{{'\xa0'}}respirer un peu, bouger et profiter de la ville{{'\xa0'}}¬ª en toute s√©curit√© <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32651&amp;ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_communiques/communiques">R√©f{{'\xa0'}}&#x1f855;</a>. En date du 15 mai 2020, la Ville de Montr√©al d√©clarait que le <a class="reference-link" target="_blank" href="https://csc.maps.arcgis.com/apps/webappviewer/index.html?id=574ba931baa34f40a77a6b6181e1821f&amp;extent=-8279149.0754%2C5669447.1423%2C-8132389.9811%2C5743514.6227%2C102100"><i>Plan estival </i></a> encourageait l‚Äôactivit√© pi√©tonne et cycliste au d√©triment de la voiture par l‚Äôimplantation temporaire de voies actives s√©curitaires prenant diverses formes dans Montr√©al (corridor pi√©ton, rue pi√©tonne et piste cyclable) <a class="reference-link" target="_blank" href="https://montreal.ca/actualites/plan-estival-de-corridors-de-transports-actifs-du-plateau-mont-royal#:~:text=Plan%20estival%20de%20corridors%20de%20transports%20actifs%20du%20Plateau%2DMont%2DRoyal,-Publi%C3%A9%20le%2015&amp;text=Dans%20la%20foul%C3%A9e%20de%20l,des%20pi%C3%A9tons%20et%20des%20cyclistes.">R√©f{{'\xa0'}}&#x1f855;</a> <a class="reference-link" target="_blank" href="https://montreal.ca/articles/les-voies-actives-securitaires-un-plan-de-deplacements-estival">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Concernant la mobilit√©, <i>Le Devoir</i> informait ses lecteurs, le 30{{'\xa0'}}mars 2020, que de nombreux Montr√©alais se plaignaient au d√©but de confinement de la fermeture des commerces de r√©paration de v√©los, ceux-ci n‚Äô√©tant pas consid√©r√©s essentiels <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/transports-urbanisme/576005/des-velos-qui-nous-sont-essentiels">R√©f{{'\xa0'}}&#x1f855;</a>. Ce m√™me article de fond abordait que la crise de la COVID-19 √©tait id√©ale pour offrir{{'\xa0'}}: ¬´{{'\xa0'}}aux Qu√©b√©cois les moyens d‚Äôutiliser un mode de transport durable et abordable (v√©lo), dans les meilleures conditions possibles, pour en retirer tous les b√©n√©fices{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/transports-urbanisme/576005/des-velos-qui-nous-sont-essentiels">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>D√®s le retour du beau temps, le quotidien montr√©alais <i>La Presse</i> signalait, le 4{{'\xa0'}}mai 2020, une forte affluence dans les parcs qui engendrait certaines difficult√©s aupr√®s des autorit√©s √† faire respecter la distanciation sociale prescrite <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-05-04/forte-affluence-dans-les-parcs-la-situation-est-elle-maitrisee">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p><i>La Presse</i>, le 9{{'\xa0'}}mai 2020, √©changeait avec des experts en design urbain qui soulevaient l‚Äôimportance d‚Äôespaces publics vastes dans le paysage urbain, d‚Äôautant plus que ceux-ci sont manquants dans certains quartiers <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/ff83c3e0-b983-42ae-9d85-f2de0fbcd024__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. En effet, les chercheurs rapportaient √† <i>La Presse</i>, lors d‚Äôune entrevue, un acc√®s in√©gal aux parcs en fonction du niveau √©conomique de la population des diff√©rents arrondissements. Ils soul√®vent aussi l‚Äôimportance de donner acc√®s √† ces parcs par des pistes cyclables ou des corridors pi√©tons, sinon ils deviennent seulement accessibles √† la population poss√©dant une automobile <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/ff83c3e0-b983-42ae-9d85-f2de0fbcd024__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p class="conclusion">La p√©riode de confinement encourageait la population √† multiplier les sorties dans les parcs publics montr√©alais. De plus, en regard des articles rep√©r√©s, de nombreux Montr√©alais √©taient d√©√ßus que les r√©parateurs de v√©lo ne soient pas consid√©r√©s comme des commerces essentiels (√©picerie, pharmacie, etc.) au d√©but du confinement, leur privant ainsi des services associ√©s √† l‚Äôusage du v√©lo. √Ä la fin du confinement, l‚Äôannonce de la mise en place d‚Äôun ¬´{{'\xa0'}}<i>Plan estival</i>{{'\xa0'}}¬ª favorisant les d√©placements √† pied et √† v√©lo laisse entrevoir des changements importants dans l‚Äôespace urbain. Les changements dans l‚Äôespace public rapport√©s par les m√©dias, tels les pistes cyclables et les rues pi√©tonnes diversifient l‚Äôexp√©rience paysag√®re montr√©alaise, malgr√© la contrainte de la distanciation physique. Par le r√©am√©nagement de certaines rues commerciales et par l‚Äôajout de pistes cyclables, le ¬´{{'\xa0'}}<i>Plan estival</i>{{'\xa0'}}¬ª devrait contribuer √† cr√©er de nouvelles perspectives paysag√®res (ambiances, animation, r√©appropriation de l‚Äôespace public) dans les rues montr√©alaises. Par ailleurs, le confinement, qui contraignait les Montr√©alais √† leur habitation, a sembl√© les inciter √† se retrouver √† l‚Äôext√©rieur, √† renouer des contacts avec la nature et √† explorer de mani√®re diff√©rente le paysage urbain montr√©alais.</p>
			
			<h2>Am√©nagement / <br/><em>confinement</em></h2>
				<h3>5 - Quelles ont √©t√© les directives √©mises par les autorit√©s gouvernementales et municipales pour l'utilisation des espaces publics, des espaces collectifs, etc.{{'\xa0'}}? </h3>
					<p>Pour l‚Äôensemble du Qu√©bec, le gouvernement du Qu√©bec imposait des mesures de distanciation de deux m√®tres entre chaque individu de diff√©rents m√©nages √† partir du 18{{'\xa0'}}mars 2020 <a class="reference-link" target="_blank" href="http://www.fil-information.gouv.qc.ca/Pages/Article.aspx?TaillePage=50&amp;Page=47&amp;idArticle=2803190229">R√©f{{'\xa0'}}&#x1f855;</a>. La Ville de Montr√©al annon√ßait la fermeture des modules de jeu pour enfants, le 22{{'\xa0'}}mars 2020 <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32483&amp;ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_communiques/communiques">R√©f{{'\xa0'}}&#x1f855;</a>, et les parcs canins, le 28{{'\xa0'}}mars 2020 <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32509&amp;ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_communiques/communiques">R√©f{{'\xa0'}}&#x1f855;</a>, pour freiner la contagion de la COVID-19. √âgalement, en date du 11{{'\xa0'}}avril 2020, le journal <i>Le Devoir</i> indiquait que le gouvernement du Qu√©bec, appuy√© par la Ville de Montr√©al, for√ßait l'annulation de tout √©v√®nement public pour l‚Äô√©t√© 2020. Les espaces publics √©v√®nementiels, tel le parc Jean-Drapeau sur l'√Æle Sainte-H√©l√®ne, ne seraient donc pas √† disposition des citoyens cette ann√©e <a class="reference-link" target="_blank" href="https://www.ledevoir.com/politique/quebec/576846/un-ete-sans-culture-ni-sport-a-quebec">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Concernant les parcs, <i>Le Devoir</i>, le 2{{'\xa0'}}mai 2020, d√©voilait que la Ville de Montr√©al en fermant les stationnements dans les parcs, pour √©viter les d√©placements dans diff√©rents arrondissements, incitait ses citoyens √† visiter uniquement les parcs √† proximit√© de leur milieu de vie <a class="reference-link" target="_blank" href="https://www.ledevoir.com/politique/montreal/578190/montreal-ferme-d-autres-stationnements-de-parcs?utm_medium=Social&amp;utm_campaign=Autopost&amp;utm_source=Facebook#Echobox=1588466291">R√©f{{'\xa0'}}&#x1f855;</a>. Quant aux rassemblements dans les parcs, <i>La Presse</i>, un quotidien, le 5{{'\xa0'}}mai 2020, se montrait critique face aux directives des autorit√©s indiquant qu‚Äôil √©tait interdit de se rassembler dans les parcs. Cependant selon les d√©crets, si chaque individu de diff√©rents m√©nages se tenait √† deux m√®tres de distance, aucune contravention ne pouvait √™tre donn√©e <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-05-04/forte-affluence-dans-les-parcs-la-situation-est-elle-maitrisee">R√©f{{'\xa0'}}&#x1f855;</a> <a class="reference-link" target="_blank" href="https://www.lapresse.ca/debats/editoriaux/2020-05-05/les-rassemblements-sont-interdits-vraiment">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Pour les espaces collectifs int√©rieurs publics, tels les centres communautaires, les lieux culturels, biblioth√®ques, piscines, ar√©nas, installations sportives et chalets dans les parcs, aucune directive d‚Äôutilisation n‚Äôavait √©t√© donn√©e par les autorit√©s, car au d√©but du confinement la Ville de Montr√©al annon√ßait dans un communiqu√© de presse du 28{{'\xa0'}}mars 2020 qu‚Äôils demeuraient ferm√©s jusqu‚Äô√† nouvel ordre <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32509&amp;ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_communiques/communiques">R√©f{{'\xa0'}}&#x1f855;</a>. Cependant, <i>La Gazette</i>, journal montr√©alais, rapportait, le 15 avril 2020, que les jardins communautaires et les p√©pini√®res √©taient consid√©r√©s des services essentiels suite √† la forte demande citoyenne <a class="reference-link" target="_blank" href="https://montrealgazette.com/news/local-news/gardeners-rejoice-as-nurseries-and-garden-centres-allowed-to-reopen/">R√©f{{'\xa0'}}&#x1f855;</a>. √Ä ce sujet, la Ville de Montr√©al √©non√ßait des directives dans un de ses communiqu√©s de presse du 30{{'\xa0'}}avril 2020 appelant √† respecter la distanciation de deux m√®tres en suivant un horaire pr√©cis sans partager ses outils et en soulignant l‚Äôimportance de nettoyer fr√©quemment les espaces communs <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32604">R√©f{{'\xa0'}}&#x1f855;</a>. De plus, Laurence Lavigne Lalonde, responsable de la transition √©cologique et de la r√©silience, d'Espace pour la vie et de l'agriculture urbaine au sein du comit√© ex√©cutif de la Ville de Montr√©al d√©clarait, le 30 avril 2020, dans un communiqu√© de presse que{{'\xa0'}}: ¬´{{'\xa0'}}Cultiver Montr√©al et la Ville de Montr√©al partagent la volont√© de favoriser l'essor de la pratique agricole √† domicile. [‚Ä¶] C'est l'occasion d'am√©liorer notre autosuffisance alimentaire en cultivant, chez nous, les produits dont nous avons besoin.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32604">R√©f{{'\xa0'}}&#x1f855;</a>. La Ville de Montr√©al indiquait, le m√™me jour, que le Jardin botanique | Espace pour la vie : ¬´{{'\xa0'}}a augment√© sa surface de production de plantes potag√®res. En tout, un hectare de jardins servira √† la production de l√©gumes et fruits vari√©s, ce qui permettra [‚Ä¶] de remettre plus du double de la quantit√© qu'il remet habituellement √† des organismes communautaires.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32604">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>La Ville de Montr√©al annon√ßait, le 29{{'\xa0'}}avril 2020, dans un de ses communiqu√©s de presse qu‚Äôen amont d‚Äôinformer en ligne les citoyens, elle appelait personnellement les personnes √¢g√©es ne vivant pas dans des r√©sidences pour s‚Äôassurer qu‚Äôelles √©taient inform√©es des directives et de la situation particuli√®re <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32602">R√©f{{'\xa0'}}&#x1f855;</a>. <i>Le Devoir</i> signalait, le 5{{'\xa0'}}mai 2020, que les a√Æn√©s s‚Äôav√©raient oblig√©s de faire face √† des mesures strictes concernant les sorties (limite de temps), auxquelles les autres personnes hors des centres √©taient dispens√©es. Pour ces raisons, une r√©sidente de 76 ans du Manoir Pierrefonds, C√©cile Boeck{{'\xa0'}}: ¬´{{'\xa0'}}en appelle √† l‚Äôindignation, c‚Äôest nous infantiliser, c‚Äôest une atteinte √† notre dignit√©. On n‚Äôest pas inaptes.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/578307/la-revolte-des-aines?utm_medium=Social&amp;utm_campaign=Autopost&amp;utm_source=Facebook#Echobox=1588654595">R√©f{{'\xa0'}}&#x1f855;</a>. <i>Le Devoir</i> indiquait que le gouvernement du Qu√©bec tentait de rem√©dier √† la situation en clarifiant que ces mesures ne s'appliquaient pas aux a√Æn√©s en bonne sant√© <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/578307/la-revolte-des-aines?utm_medium=Social&amp;utm_campaign=Autopost&amp;utm_source=Facebook#Echobox=1588654595">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Pour le transport en commun, <i>Le Devoir</i> mentionnait, le 5 mai 2020, que la <i>Soci√©t√© de transport de Montr√©al</i> (STM) recommandait fortement le port du masque pour les usagers de son r√©seau, cependant elle ne pouvait l‚Äôobliger lors de cette phase du confinement <a class="reference-link" target="_blank" href="https://www.ledevoir.com/politique/montreal/578339/la-stm-installera-des-panneaux-de-plexiglas-dans-ses-autobus?fbclid=IwAR3t3Xh9keHXt_bEL6CNB1xTmnrurYhKgjxpl9Q43-yqjGSdBKqr1b45-LM">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p class="conclusion">Les directives √©mises par les autorit√©s gouvernementales et municipales pour l‚Äôutilisation des espaces publics √©taient identiques pour l‚Äôensemble du territoire montr√©alais et qu√©b√©cois. Elles demandaient de respecter une distanciation de deux m√®tres entre les personnes issues de diff√©rents m√©nages dans les espaces publics. Ces mesures s‚Äôappliquaient pour les visites au parc, dans les jardins communautaires et aussi pour tous les autres usages de l‚Äôespace public. En ce qui a trait aux espaces collectifs int√©rieurs, aucune directive n‚Äôa √©t√© √©mise par les autorit√©s municipales concernant leur utilisation, car ils sont demeur√©s ferm√©s pour l‚Äôenti√®ret√© du confinement. Cependant, pour le transport en commun qui est essentiel au d√©placement des Montr√©alais, les autorit√©s municipales ne pouvaient obliger le port du masque lors du confinement, mais le recommandaient fortement, car la distanciation √©tait difficile √† respecter. Les articles de presse ont relev√© que malgr√© l‚Äôobligation de respecter les directives de distanciation entre les personnes et l‚Äôinterdiction de rassemblements, il demeurait l√©gal de se rassembler dans les lieux publics tout en respectant deux m√®tres de distanciation. Concernant les directives sp√©cifiques de droit de sortie dans les centres de personnes √¢g√©es (CHSLD et autres), de nombreuses critiques ont √©t√© √©mises indiquant qu‚Äôelles brimaient leur droit de sortie et √©taient interpr√©t√©es comme une contrainte dans leurs activit√©s quotidiennes.</p>
				<h3>6 - Durant la p√©riode de confinement montr√©alaise, quelles sont les actions qui ont √©t√© d√©ploy√©es dans les espaces publics{{'\xa0'}}?</h3>
					<p>Le journal montr√©alais <i>La Presse</i> indiquait que, depuis le 23{{'\xa0'}}mars 2020, la place des Festivals (Quartier des spectacles, Montr√©al) h√©bergeait temporairement une clinique de d√©pistage de la COVID-19 <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-04-20/la-clinique-covid-demantelee-au-centre-ville-de-montreal">R√©f{{'\xa0'}}&#x1f855;</a>. Cette clinique r√©pondait aux demandes de d√©pistage en attendant que les infrastructures montr√©alaises s‚Äôadaptent. En date du 20{{'\xa0'}}avril 2020, ce quotidien annon√ßait la fermeture de cette clinique <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-04-20/la-clinique-covid-demantelee-au-centre-ville-de-montreal">R√©f{{'\xa0'}}&#x1f855;</a>. De plus, le Centre int√©gr√© universitaire de sant√© et de services sociaux (CIUSS) du Centre-Sud-de-l‚Äô√éle-de-Montr√©al signalait, le 14 avril 2020, l‚Äôouverture d‚Äôune annexe √† l‚ÄôH√¥pital de Verdun dans un stationnement municipal public √† proximit√© de celui-ci <a class="reference-link" target="_blank" href="https://ciusss-centresudmtl.gouv.qc.ca/actualite/covid-19-ouverture-prochaine-dune-annexe-lhopital-de-verdun">R√©f{{'\xa0'}}&#x1f855;</a>. Similairement, la municipalit√© montr√©alaise d√©clarait, le 24 avril 2020, l‚Äôextension du CIUSSS de l'Ouest-de-l'√éle-de-Montr√©al dans le stationnement de l‚Äôar√©na Jacques-Lemaire √† LaSalle par l‚Äôimplantation d‚Äôun h√¥pital de campagne <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32590">R√©f{{'\xa0'}}&#x1f855;</a>. <i>Radio-Canada </i>mentionnait, le 21 avril 2020, que des patients √©taient trait√©s au sein de l‚Äôh√¥tel Place Dupuis. √Ä ce sujet, la directrice g√©n√©rale de l'h√¥tel, Nadia Francolini, soulignait que{{'\xa0'}}: ¬´{{'\xa0'}}quand la demande est venue, nous avons r√©pondu √† l'appel. Nous sommes fiers de former un partenariat avec le CIUSSS-EMTL. Ceci est notre fa√ßon comme h√¥telier d'aider notre r√©gion dans ce moment de pand√©mie.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1696033/coronavirus-place-dupuis-patients-chambres-hotel">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Le journal <i>La Gazette</i> informait la population, le 14 avril 2020, que la distanciation sociale √©tait la mesure la plus efficace pour diminuer la dispersion de la COVID-19 selon les autorit√©s en sant√© publique au Qu√©bec. Dans ce m√™me article, ce quotidien annon√ßait aussi l‚Äôimplantation des premiers corridors sanitaires sur l‚Äôavenue Mont-Royal dans l‚Äôarrondissement du Plateau Mont-Royal pour aider les citoyens √† respecter la distanciation lors de leurs achats essentiels <a class="reference-link" target="_blank" href="https://montrealgazette.com/business/local-business/montreal-to-decide-on-new-health-corridors-by-weeks-end-plateau-mayor/">R√©f{{'\xa0'}}&#x1f855;</a>. <i>Radio-Canada</i>, le 17{{'\xa0'}}avril 2020, d√©crivait que les corridors sanitaires consistaient √† retirer les deux all√©es de stationnement en bordure des avenues commer√ßantes, ainsi un espace pi√©tonnier √©largi est offert aux Montr√©alais <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694988/trottoir-elargi-corridors-sanitaires-commerces-services?fromApp=appInfoIos&amp;partageApp=appInfoiOS&amp;accesVia=partage">R√©f{{'\xa0'}}&#x1f855;</a>. Dans la foul√©e de l‚Äôimplantation de corridors sanitaires, la municipalit√© mentionnait observer des retomb√©es positives de ce nouvel attribut urbain. Elle divulguait, le 17 avril 2020, qu‚Äôelle proc√©derait √† la multiplication des corridors sanitaires dans les arrondissements suivants{{'\xa0'}}: Ahuntsic-Cartierville, Le Plateau-Mont-Royal, C√¥te-Des-Neiges-Notre-Dame-De-Gr√¢ce, Outremont, Rosemont-La-Petite-Patrie, Mercier‚ÄìHochelaga-Maisonneuve, Verdun, Ville-Marie et le Sud-Ouest <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32575">R√©f{{'\xa0'}}&#x1f855;</a>. Cependant, dans un article de <i>La Presse</i> dat√© du 11 mai 2020, Rapha√´lle Auger, qui habite l‚Äôarrondissement Mercier-Hochelaga-Maisonneuve, t√©moignait de l‚Äôabsence de corridors sanitaires sur la rue commer√ßante Ontario <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/f510fc25-89f7-44f3-8e93-a498f2b6f6c4__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Concernant l‚Äôam√©nagement de l‚Äôespace public, le quotidien <i>La Presse</i> rapportait, le 8 mai 2020, que l‚Äôarrondissement Rosemont-La-Petite-Patrie s‚Äô√©tait inspir√© du concept catalan des <i>superblocs</i>. Cet arrondissement a divis√© son territoire en diff√©rents secteurs et a d√©termin√© des rues avec circulation de transit automobile permise, alors que d‚Äôautres sont r√©serv√©es seulement √† la circulation automobile lente et locale <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/c475f3bf-ca44-4fe5-93e1-ef38de7e2a2c__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. En amont, le m√™me article divulguait que cet arrondissement ajoutait 50 km √† son r√©seau de pistes cyclables <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/c475f3bf-ca44-4fe5-93e1-ef38de7e2a2c__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>En date du 6{{'\xa0'}}mai 2020, la Ville de Montr√©al communiquait la pi√©tonnisation de l‚Äôavenue Wellington, une art√®re commerciale dans l‚Äôarrondissement de Verdun (<a class="reference-link" target="_blank" href="https://montreal.ca/actualites/declaration-sur-la-pietonnisation-de-la-rue-wellington">R√©f{{'\xa0'}}&#x1f855;</a>). D‚Äôautre part, dans un article datant du 13 mai 2020, <i>La Presse</i> avisait que le <a class="reference-link" target="_blank" href="https://csc.maps.arcgis.com/apps/webappviewer/index.html?id=574ba931baa34f40a77a6b6181e1821f&amp;extent=-8279149.0754%2C5669447.1423%2C-8132389.9811%2C5743514.6227%2C102100"><i>Plan</i></a><i> estival de la Ville de Montr√©al</i>{{'\xa0'}}¬ª r√©serverait plusieurs rues commer√ßantes √† Montr√©al seulement aux pi√©tons <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-05-13/le-plan-estival-d-amenagement-urbain-de-montreal-devoile-vendredi">R√©f{{'\xa0'}}&#x1f855;</a>. Ainsi, la Ville de Montr√©al annon√ßait, le 15{{'\xa0'}}mai 2020, lors du d√©voilement du ¬´{{'\xa0'}}<a class="reference-link" target="_blank" href="https://csc.maps.arcgis.com/apps/webappviewer/index.html?id=574ba931baa34f40a77a6b6181e1821f&amp;extent=-8279149.0754%2C5669447.1423%2C-8132389.9811%2C5743514.6227%2C102100"><i>Plan estival de la Ville de Montr√©al</i></a> que d‚Äôautres art√®res commerciales (Avenue Mont-Royal Est et Ouest, rue Sainte-Catherine Est, rue de la Commune, rue Ontario) seraient pi√©tonnis√©es dans les arrondissements montr√©alais (<a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32651&amp;ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_communiques/communiques">R√©f{{'\xa0'}}&#x1f855;</a>)(<a class="reference-link" target="_blank" href="https://montreal.ca/articles/les-voies-actives-securitaires-un-plan-de-deplacements-estival">R√©f{{'\xa0'}}&#x1f855;</a>). En amont, la Ville de Montr√©al d√©clarait, le 15{{'\xa0'}}mai 2020, la mise en ≈ìuvre d‚Äôun r√©seau de pistes cyclables rejoignant les grands parcs montr√©alais, soit Mont-Royal, Maisonneuve, Jarry, Fr√©d√©ric-Back et l'√éle-de-la-Visitation <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32659">R√©f{{'\xa0'}}&#x1f855;</a>. Les nouvelles mesures pour favoriser la distanciation sociale, les pi√©tons et les cyclistes √©taient nomm√©es voies actives s√©curitaires par la Ville de Montr√©al <a class="reference-link" target="_blank" href="https://montreal.ca/articles/les-voies-actives-securitaires-un-plan-de-deplacements-estival">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>De mani√®re critique, <i>Le Devoir</i> (14{{'\xa0'}}avril 2020) et <i>La Presse</i> (16{{'\xa0'}}mai 2020) supposaient qu‚Äôune appr√©ciation positive des citoyens face √† ces mesures pourrait entrainer davantage d‚Äôinitiatives en mobilit√© lente √† Montr√©al <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/576952/coronavirus-la-vie-a-deux-metres-de-distance">R√©f{{'\xa0'}}&#x1f855;</a> <a class="reference-link" target="_blank" href="http://plus.lapresse.ca/screens/515d6d89-7cf2-44fb-aec9-93fbf0a63d73__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. Le journaliste Bruno Brisson partageait √† <i>La Presse</i>, le 29 avril 2020, un √©ditorial sur l‚Äôapr√®s confinement √† Montr√©al. Dans celui-ci, il d√©clarait que la reconsid√©ration que la Ville de Montr√©al effectuait au sujet de l‚Äôespace public d√©di√© aux pi√©tons et aux cyclistes pourrait mener √† un d√©couragement de l‚Äôutilisation automobile tout en encourageant une mobilit√© √©cologique <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/92e5b312-bc91-41af-a136-88b25cddf616__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. Dans cet √©ditorial, Catherine Morency, une sp√©cialiste en mobilit√© commentait que{{'\xa0'}}: ¬´{{'\xa0'}}le d√©fi des prochaines ann√©es dans les villes, ce sera la distribution de l‚Äôespace urbain. C‚Äôest comme s‚Äôil y avait une comp√©tition pour savoir qui a le droit d‚Äôoccuper un espace public, dans la rue ou au bord de la rue.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-04-29/mobilite-montreal-prepare-l-apres-confinement">R√©f{{'\xa0'}}&#x1f855;</a>. Selon un √©ditorial de <i>La Presse</i> publi√© le 2 mai 2020, le journaliste Fran√ßois Cardinal expliquait qu‚Äô{{'\xa0'}}: ¬´{{'\xa0'}}il faut saisir l‚Äôoccasion unique qui se pr√©sente √† nous pour faire de la m√©tropole un milieu de vie convivial qui nous aide collectivement √† passer √† travers un √©t√© qui s‚Äôannonce hors de l‚Äôordinaire.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/b1b9d8a3-153b-4298-b75e-ff1074ad6327__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. Il a aussi insist√© sur la pratique du v√©lo et de la marche qui permet aux gens de se d√©placer de mani√®re distanc√©e et √©cologique <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/b1b9d8a3-153b-4298-b75e-ff1074ad6327__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. L‚Äô√©ditorialiste se montrait favorable{{'\xa0'}}au{{'\xa0'}}changement{{'\xa0'}}: ¬´{{'\xa0'}}la pand√©mie est l‚Äôoccasion ou jamais d‚Äôexp√©rimenter une ville √† √©chelle humaine, o√π la marche et le v√©lo permettent une communion entre les personnes qui partagent l‚Äôespace public{{'\xa0'}}¬ª. Dans un √©ditorial du <i>Devoir</i> datant du 11{{'\xa0'}}mai 2020, l‚Äôurbanisme tactique √©tait abord√© par le professeur Nicolas Saunier, membre de la <i>Chaire Mobilit√© de Polytechnique Montr√©al</i> : ¬´{{'\xa0'}}√ßa offre l‚Äôoccasion de faire de l‚Äôurbanisme tactique, de tester des choses rapidement et d‚Äôobserver comment les am√©nagements fonctionnent pour les adapter. On enl√®ve, on d√©place, on am√©liore{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/transports-urbanisme/578683/place-aux-velos">R√©f{{'\xa0'}}&#x1f855;</a>. Le professeur Philippe Apparicio, de <i>l‚ÄôInstitut national de la recherche scientifique</i> livrait dans ce m√™me √©ditorial qu‚Äô{{'\xa0'}}: ¬´{{'\xa0'}}√† ses yeux, en offrant des am√©nagements s√©curitaires et une exp√©rience agr√©able sur deux roues, la Ville de Montr√©al pourrait aller chercher une client√®le qui n‚Äô√©tait pas habitu√©e √† faire du v√©lo au quotidien et la fid√©liser.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/transports-urbanisme/578683/place-aux-velos">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>En ce qui concerne les initiatives d√©ploy√©es dans l'espace public pour aider les itin√©rants, la Ville de Montr√©al indiquait dans un communiqu√© de presse du 20 mars 2020 avoir mis sur place cinq centres de jour pour offrir des repas, l‚Äôacc√®s √† des stations sanitaires et de l‚Äôaide d‚Äôintervenants sociaux <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32581">R√©f{{'\xa0'}}&#x1f855;</a>. Ces centres de jour se trouvaient √† l‚Äôar√©na Francis-Bouillon, la place √âmilie-Gamelin, le square Cabot, le parc Jeanne-Mance et la Place du Canada <a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/pls/portal/docs/PAGE/ARROND_VMA_FR/MEDIA/DOCUMENTS/CARTE%20TOILETES%20BLOCS%20SANITAIRES%20JUIN%202020.PDF">R√©f{{'\xa0'}}&#x1f855;</a>. <i>Radio-Canada</i>, le 8{{'\xa0'}}avril 2020, rapportait que la Ville de Montr√©al r√©quisitionnait quatre h√¥tels pour h√©berger les itin√©rants et leur offrir un milieu de vie r√©pondant √† leurs besoins <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1691992/hotels-sans-abris-itinerants-covid-toronto-montreal">R√©f{{'\xa0'}}&#x1f855;</a>. Le 15{{'\xa0'}}avril 2020, <i>Radio-Canada</i> mentionnait que la Ville de Montr√©al mettait en ≈ìuvre des haltes-r√©pit permettant aux itin√©rants de se reposer et de se r√©chauffer durant la journ√©e <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694077/montreal-haltes-ouverture-personnes-sans-abri-itinerant-coronavirus">R√©f{{'\xa0'}}&#x1f855;</a>. Ces haltes-r√©pit se situaient au Coll√®ge Dawson, √† la Grande Biblioth√®que, √† l‚Äôint√©rieur de l‚Äôar√©na Francis-Bouillon et dans l‚Äô√âglise anglicane Saint-Georges <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694077/montreal-haltes-ouverture-personnes-sans-abri-itinerant-coronavirus">R√©f{{'\xa0'}}&#x1f855;</a>. Ce m√™me article indiquait l‚Äôouverture de plusieurs centres d‚Äôh√©bergement d‚Äôurgence. Ceux-ci √©taient localis√©s √† l‚Äôar√©na Maurice-Richard, le Complexe Guy-Favreau, le YMCA centre-ville, le march√© Bonsecours et le centre Jean-Claude-Mal√©part <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694077/montreal-haltes-ouverture-personnes-sans-abri-itinerant-coronavirus">R√©f{{'\xa0'}}&#x1f855;</a>. La mairesse de la Ville de Montr√©al, Val√©rie Plante √† propos de ces nouvelles mesures affirmait, le 20 avril 2020, dans un communiqu√© de presse que{{'\xa0'}}: ¬´{{'\xa0'}}nous avons la ferme volont√© de ne pas laisser la crise sanitaire que nous vivons devenir une crise humanitaire. C'est pourquoi nous continuerons d'intervenir tant que cela sera n√©cessaire. Nous ne laisserons personne derri√®re{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://www.newswire.ca/fr/news-releases/covid-19-la-ville-de-montreal-deploie-une-nouvelle-serie-de-mesures-pour-soutenir-les-personnes-en-situation-d-itinerance-876843727.html?fbclid=IwAR3XsK5DW-bgKSAhRiO82OS4V9gmAdgvEHIEo1afqNvtE-GrLEbyadj6wew">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p class="conclusion">Les mesures et les outils mis en ≈ìuvre √† Montr√©al concernant l‚Äôam√©nagement des espaces publics touchaient principalement la mobilit√© urbaine et l‚Äôaide aux personnes vuln√©rables, soit les itin√©rants. En ce qui a trait aux mesures et outils pour venir en aide √† ces personnes, l‚Äôespace public montr√©alais n‚Äôa pas fait l‚Äôobjet d‚Äôam√©nagement ou d‚Äôaction particuli√®re, sauf dans des lieux publics d√©j√† connus des sans-abri √† Montr√©al. L‚Äôavenir de ces initiatives, dans la p√©riode post-confinement montr√©alais, semble incertain dans les deux cas. En ce qui a trait aux initiatives dans les espaces publics occup√©s par les itin√©rants, celles-ci risquent de ne pas se poursuivre au-del√† du confinement, consid√©rant que les organismes √† but non lucratif (OBNL) pourront reprendre leurs activit√©s de soutien et d‚Äôentraide face aux itin√©rants lorsque la distanciation ne sera plus obligatoire. En regard des communiqu√©s de presse de la municipalit√©, des mesures diversifi√©es pour am√©nager l‚Äôespace public ont √©t√© mises en ≈ìuvre pour adapter la mobilit√© urbaine dans l‚Äôensemble des arrondissements. Entre autres, les corridors sanitaires √©taient, au d√©part, singuliers √† l‚Äôarrondissement du Plateau-Mont-Royal, mais ils se sont multipli√©s rapidement dans d'autres arrondissements. Suite au confinement, la Ville de Montr√©al implantait des voies actives s√©curitaires et son ¬´{{'\xa0'}}<i>Plan estival</i>{{'\xa0'}}¬ª sur l‚Äôensemble du territoire pour favoriser la circulation √† pied et √† v√©lo entre les grands parcs montr√©alais. Toutefois, l‚Äôarrondissement de Rosemont-La-Petite-Patrie organisait de mani√®re locale et singuli√®re la division de son territoire et la distribution de la mobilit√© (automobile, v√©lo, marche) en s‚Äôinspirant du concept catalan des <i>superblocs</i>. Les corridors sanitaires et les voies actives s√©curitaires ont sembl√© faciliter la distanciation physique et encourager la mobilit√© lente au d√©triment des d√©placements en voiture. √Ä court terme, au lendemain du confinement, la r√©alisation (r√©am√©nagement urbain) du ¬´{{'\xa0'}}<i>Plan estival</i>{{'\xa0'}}¬ª devrait encourager, au cours de l‚Äô√©t√©, les Montr√©alais √† se d√©placer √† v√©lo et √† pied. Ce plan devrait favoriser une convivialit√© urbaine avec les am√©nagements temporaires envisag√©s sur rue et r√©duire ainsi l‚Äôempreinte √©cologique de la circulation automobile. Cependant, √† long terme, le changement des saisons limitera sans doute les impacts des nouveaux am√©nagements dans l‚Äôespace public en raison du climat nordique montr√©alais.</p>
				<h3>7 - Quels ont √©t√© les d√©fis et les enjeux associ√©s envers les art√®res commerciales et les mesures sanitaires{{'\xa0'}}?</h3>
					<p>Depuis le 17{{'\xa0'}}avril 2020, la Ville de Montr√©al avait mis en ≈ìuvre sur les rues commer√ßantes de plusieurs arrondissements (voir la liste √† la question 6) une nouvelle mesure qui se nomme le corridor sanitaire (<a class="reference-link" target="_blank" href="http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&amp;_dad=portal&amp;_schema=PORTAL&amp;id=32575">R√©f{{'\xa0'}}&#x1f855;</a>). De plus tel qu‚Äôexpliqu√© √† la question pr√©c√©dente, pour s‚Äôadapter aux mesures de distanciation sanitaires et relancer les art√®res commerciales, plusieurs d‚Äôentre elles sont devenues pi√©tonnes temporairement pour l‚Äô√©t√© 2020.</p>
					<p>Suite aux annonces de la Ville de Montr√©al, <i>Radio-Canada</i>, source de nouvelles quotidiennes, le 17{{'\xa0'}}avril 2020, informait les internautes que l‚Äôaccueil des corridors sanitaires sur les grandes art√®res commerciales des quartiers montr√©alais permettait de respecter la distanciation sociale (<a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694988/trottoir-elargi-corridors-sanitaires-commerces-services?fromApp=appInfoIos&amp;partageApp=appInfoiOS&amp;accesVia=partage">R√©f{{'\xa0'}}&#x1f855;</a>). <i>Radio-Canada</i> expliquait qu‚Äôun corridor sanitaire consiste √† offrir plus d‚Äôespace pour les pi√©tons pour circuler et faire la file d‚Äôattente au d√©triment des espaces de stationnement dans la rue (<a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1694988/trottoir-elargi-corridors-sanitaires-commerces-services?fromApp=appInfoIos&amp;partageApp=appInfoiOS&amp;accesVia=partage">R√©f{{'\xa0'}}&#x1f855;</a>). </p>
					<p>√Ä propos des commerces, <i>Le Devoir</i>, le 4{{'\xa0'}}mai 2020, indiquait que les commerces sur rue √©taient ouverts dans l‚Äôensemble du Qu√©bec, √† l‚Äôexception de Montr√©al (<a class="reference-link" target="_blank" href="https://www.ledevoir.com/politique/quebec/577857/coronavirus-point-de-presse-legault-28-avril">R√©f{{'\xa0'}}&#x1f855;</a>). En effet, le <i>Journal M√©tro</i> rapportait que les commerces sur rue seraient ouverts seulement √† partir du 25{{'\xa0'}}mai 2020 (<a class="reference-link" target="_blank" href="https://journalmetro.com/actualites/montreal/2446834/les-commerces-pourront-rouvrir-le-25-mai-a-montreal/">R√©f{{'\xa0'}}&#x1f855;</a>). De plus, cette source affirmait aussi que les centres commerciaux ne pr√©sentaient pas de dates de r√©ouverture (<a class="reference-link" target="_blank" href="https://journalmetro.com/actualites/montreal/2446834/les-commerces-pourront-rouvrir-le-25-mai-a-montreal/">R√©f{{'\xa0'}}&#x1f855;</a>). </p>
					<p>Une pr√©occupation ressortait concernant les terrasses de restaurants sur les art√®res commerciales. En effet, Natha√´lle Morissette de <i>La Presse</i> soulevait, le 24{{'\xa0'}}mars 2020, que les Montr√©alais craignaient ne pas avoir de terrasses sur les rues de la ville pour la p√©riode estivale (<a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/29aa14be-d20a-4ee3-8c90-f08545f6f572__7C___0.html?utm_medium=Email&amp;utm_campaign=Internal+Share&amp;utm_content=Screen">R√©f{{'\xa0'}}&#x1f855;</a>). D‚Äôautre part, Jeanne Corriveau et Pauline Gravel, dans un √©ditorial au <i>Devoir </i>publi√© le 30{{'\xa0'}}avril 2020, proposaient de fermer les rues aux automobiles pour avoir acc√®s √† davantage d‚Äôespace pour que les restaurants aient des terrasses distanci√©es (<a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/577987/un-ete-sans-fraicheur-ni-trempette">R√©f{{'\xa0'}}&#x1f855;</a>). Cette mesure s‚Äô√©tait concr√©tis√©e par le ¬´{{'\xa0'}}<a class="reference-link" target="_blank" href="https://csc.maps.arcgis.com/apps/webappviewer/index.html?id=574ba931baa34f40a77a6b6181e1821f&amp;extent=-8279149.0754%2C5669447.1423%2C-8132389.9811%2C5743514.6227%2C102100"><i>Plan estival de la Ville de Montr√©al</i></a> d√©voil√©e le 15{{'\xa0'}}mai 2020.</p>
					<p class="conclusion">Les d√©fis et les enjeux associ√©s envers les art√®res commerciales et les mesures sanitaires sont nombreux. √Ä Montr√©al, les art√®res commerciales sont compos√©es d‚Äôun espace commercial, soit un local int√©rieur (et d‚Äôune terrasse adjacente), le trottoir et la rue. Lors du confinement, l‚Äôimplantation de corridors sanitaires a forc√© l‚Äôempi√©tement de l‚Äôespace public (voie de circulation automobile) en raison de la largeur restreinte des trottoirs dans plusieurs quartiers montr√©alais. Par ailleurs, le fonctionnement des commerces essentiels a n√©cessit√© la cr√©ation de zones d‚Äôattente commerciale ext√©rieures sur les trottoirs rel√©guant ainsi le pi√©ton sur les voies de circulation. Ouverts aux intemp√©ries, ces espaces d‚Äôattente, m√™me s‚Äôils ont √©t√© efficaces durant le printemps et l‚Äô√©t√©, devront prouver leur pertinence durant la saison hivernale. Ainsi, les am√©nagements ext√©rieurs dans le domaine public devront sans doute √™tre repens√©s. En regard des informations recueillies, l‚Äôouverture des magasins (25{{'\xa0'}}mai 2020) et des restaurants (22{{'\xa0'}}juin 2020) √† Montr√©al durant la p√©riode de d√©confinement a g√©n√©r√© de nouveaux espaces commerciaux (ex.{{'\xa0'}}: terrasses de restaurant) sur rue privatisant ainsi l‚Äôespace public (trottoirs, voies de circulation). </p>
				<h3>8 - La mobilit√© des travailleurs dans le territoire urbain a-t-elle fait l‚Äôobjet de pr√©occupations lors du confinement montr√©alais{{'\xa0'}}?</h3>
					<p><i>La Presse</i>, le 29{{'\xa0'}}avril 2020, dans un article de fond, indiquait qu‚Äôune redistribution des horaires des travailleurs permettrait de r√©duire la congestion sur les routes <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-04-29/mobilite-montreal-prepare-l-apres-confinement">R√©f{{'\xa0'}}&#x1f855;</a>. Ce m√™me article partageait aussi une √©tude de la <i>Chaire de mobilit√© de l‚Äô√©cole Polytechnique</i> qui √©voque que si le t√©l√©travail restait en place pour le cinqui√®me des travailleurs, il engendrait une baisse de 10% des √©missions de gaz √† effet de serre au Qu√©bec <a class="reference-link" target="_blank" href="https://www.lapresse.ca/covid-19/2020-04-29/mobilite-montreal-prepare-l-apres-confinement">R√©f{{'\xa0'}}&#x1f855;</a>. L‚Äô√©ditorial affirmait que des ing√©nieurs de la firme <i>Intervia</i> d√©veloppaient une application pour permettre de pr√©venir les utilisateurs de la <i>Soci√©t√© de transport de Montr√©al</i> (STM) de l‚Äôachalandage dans les autobus <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/92e5b312-bc91-41af-a136-88b25cddf616__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. De plus, dans <i>Le Devoir</i>, le 8{{'\xa0'}}mai 2020, la STM confiait entamer des discussions avec la <i>Chambre de commerce du Montr√©al m√©tropolitain </i>(CCMM) pour encourager les employeurs √† √©taler les horaires des travailleurs sur diff√©rentes plages et par le fait m√™me r√©duire l‚Äôachalandage des passagers aux heures de pointe <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/transports-urbanisme/578594/la-stm-distribuera-des-masques-a-ses-usagers">R√©f{{'\xa0'}}&#x1f855;</a>. Malgr√© ses efforts, la STM confiait √† <i>Radio-Canada</i>, le 11{{'\xa0'}}mai 2020, que les mesures de distanciation ne pouvaient √™tre respect√©es dans son r√©seau en d√©montrant qu‚Äôavec un maximum de 15 personnes par bus et de 150 par m√©tro, la demande s‚Äô√©l√®verait √† 8{{'\xa0'}}000 bus et 800 rames de m√©tro, alors que la STM poss√®de seulement 1{{'\xa0'}}425 bus et 97 rames de m√©tro <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1701725/ctt-stm-transports-covid19-distanciation-impossible-deconfinement">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>D‚Äôun autre point de vue, Myl√®ne Drouin, directrice de la Direction r√©gionale de la sant√© publique de Montr√©al communiquait au <i>Journal M√©tro</i>, le 5{{'\xa0'}}mai 2020, que de nombreux travailleurs essentiels dans les centres d‚Äôh√©bergement et de soins de longue dur√©e (CHSLD) de Montr√©al r√©sidaient √† Montr√©al-Nord. Ceux-ci, pour se rendre sur leur lieu de travail, utilisaient pour la plupart les transports en commun. Consid√©rant que ces travailleurs √©taient directement en contact avec des personnes infect√©es par la COVID-19, leurs d√©placements dans les transports en commun repr√©sentaient un vecteur de contagion qui a √©t√© un facteur de la hausse des cas de COVID-19 √† Montr√©al-Nord (<a class="reference-link" target="_blank" href="https://journalmetro.com/local/montreal-nord/2443637/travailler-a-se-rendre-malade-a-montreal-nord/">R√©f{{'\xa0'}}&#x1f855;</a>).Pierre Filion, professeur √† l'√âcole d'urbanisme de l'Universit√© de Waterloo, d√©clarait √† <i>Radio-Canada</i>, le 10{{'\xa0'}}mai 2020, que{{'\xa0'}}: ¬´{{'\xa0'}}si la densit√© urbaine a certainement jou√© un r√¥le dans la propagation de la COVID-19, d'autres facteurs sont √† prendre en compte. [‚Ä¶] Les secteurs les plus touch√©s sont aussi ceux o√π les gens voyagent beaucoup. [‚Ä¶] Les transports en commun ont aussi jou√© un r√¥le dans la propagation du virus.{{'\xa0'}}¬ª (<a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1701627/coronavirus-urbanisme-architecture-quoi-ressembleront-villes-apres-pandemie#:~:text=Les%20urbanistes%20pr%C3%A9disent%20que%20la,fa%C3%A7ons%20d'am%C3%A9nager%20les%20villes.&amp;text=La%20pand%C3%A9mie%20de%20coronavirus%20nous,les%20zones%20les%20plus%20touch%C3%A9es.">R√©f{{'\xa0'}}&#x1f855;</a>).</p>
					<p>Dans le m√™me ordre d‚Äôid√©e, le 21{{'\xa0'}}avril 2020, lors de l‚Äô√©mission radio <i>Tout un matin</i> en onde √† <i>Radio-Canada</i>, l'animateur Maxime Couti√© √©changeait avec Alain Croteau, pr√©sident du Syndicat des travailleuses et des travailleurs du CIUSSS Centre-Sud-de-l'√éle-de-Montr√©al. Ce dernier soulevait que la crise s‚Äôest produite principalement dans les centres d‚Äôh√©bergement et de soins de longue dur√©e (CHSLD). De ce fait, les travailleurs sociaux et les pr√©pos√©s aux b√©n√©ficiaires √©taient les premi√®res personnes en contact avec les victimes de la COVID-19. √Ä son avis, ces m√™mes personnes, qui risquaient de contracter le virus, sont aussi celles qui se font verser le salaire le plus bas dans le syst√®me de sant√© qu√©b√©cois. M. Croteau d√©clarait aussi que les pr√©pos√©es aux b√©n√©ficiaires sont principalement des femmes racis√©es et elles re√ßoivent peu d'aide financi√®re et mat√©rielle (les √©quipements) dans les CHSLD soulignant le manque de soutien du gouvernement <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/premiere/emissions/tout-un-matin/segments/chronique/166242/femmes-racisees-exploitation-chsld-cisss-centre-sud?fbclid=IwAR2hDPTEiGt4Un7Y1lZKO8eohmLOg8WRKkZVRuEd-sPdRTH3dFjyMOiKZ5Q">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p class="conclusion">La mobilit√© des travailleurs dans le territoire urbain a √©t√© l‚Äôobjet de pr√©occupations lors du confinement montr√©alais. Malgr√© la pratique du t√©l√©travail pour certains employ√©s, plusieurs travailleurs devaient se d√©placer √† leur lieu de travail en transport en commun. Ces travailleurs augmentaient leur propre risque de contagion et celui de leur communaut√©. Cependant, le t√©l√©travail lorsqu‚Äôapplicable a permis de diminuer grandement le trafic m√©tropolitain montr√©alais et par le fait m√™me les √©missions de gaz √† effet de serre. De plus, le t√©l√©travail permet aux gens de respecter le confinement et d‚Äô√©viter la propagation de la COVID-19. La mobilit√© et le t√©l√©travail ont fait √©merger des propos sur l‚Äô√©talement urbain et ont amorc√© des d√©bats d‚Äôexperts sur la densit√© des quartiers urbains centraux. </p>
				<h3>9 - √Ä la suite du confinement montr√©alais, quels changements peut-on attendre de la part des individus, des collectivit√©s et des instances gouvernementales{{'\xa0'}}?</h3>
					<p>La Ville de Montr√©al affirmait, le 15{{'\xa0'}}mai 2020, dans un communiqu√© de presse que les voies actives s√©curitaires dans son <a class="reference-link" target="_blank" href="https://csc.maps.arcgis.com/apps/webappviewer/index.html?id=574ba931baa34f40a77a6b6181e1821f&amp;extent=-8279149.0754%2C5669447.1423%2C-8132389.9811%2C5743514.6227%2C102100"><i>Plan estival</i></a> offraient un espace aux pi√©tons et cyclistes sans le danger des automobilistes <a class="reference-link" target="_blank" href="https://montreal.ca/actualites/plan-estival-de-corridors-de-transports-actifs-du-plateau-mont-royal">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Ce changement correspond aux id√©es du journaliste, Fran√ßois Cardinal, dans un √©ditorial de <i>La Presse</i> qui divulguait, le 16{{'\xa0'}}mai 2020, que{{'\xa0'}}: ¬´{{'\xa0'}}l‚Äô√©t√© ne ressemblera sans doute √† aucun autre √©t√©. Ce sera l‚Äôoccasion de se promener, de prendre le temps, de d√©couvrir des quartiers, de visiter des art√®res commerciales. Bref, de s‚Äôapproprier la ville comme on ne l‚Äôa jamais fait.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/bcc22c32-aedc-4fb5-972f-1f67e018adf5__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>. De plus, Patrick Bonin de Greenpeace soulevait au <i>Journal M√©tro</i>, le 15{{'\xa0'}}mai 2020, que{{'\xa0'}}: ¬´{{'\xa0'}}les gens prendront rapidement go√ªt √† ces mesures qui am√©lioreront leur qualit√© de vie, leur sant√© et leur environnement. Nous esp√©rons que plusieurs de ces mesures deviendront permanentes{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://journalmetro.com/actualites/montreal/2446277/plusieurs-axes-reamenages-pour-les-pietons-et-les-cyclistes-a-montreal/">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Chez les diff√©rents groupes sociaux, <i>Le Devoir</i> rapportait, le 30{{'\xa0'}}avril 2020, que les a√Æn√©s allaient s√ªrement rester marqu√©s par la crise et avoir moins d‚Äôint√©r√™t √† retourner dans l‚Äôespace public, cependant les autres groupes sociaux attendaient particuli√®rement l‚Äôannonce du <a class="reference-link" target="_blank" href="https://csc.maps.arcgis.com/apps/webappviewer/index.html?id=574ba931baa34f40a77a6b6181e1821f&amp;extent=-8279149.0754%2C5669447.1423%2C-8132389.9811%2C5743514.6227%2C102100"><i>Plan estival de la Ville de Montr√©al</i></a> <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/577987/un-ete-sans-fraicheur-ni-trempette">R√©f{{'\xa0'}}&#x1f855;</a>. Au cours de la crise, <i>La Presse</i>, le 9{{'\xa0'}}mai 2020, mentionnait l‚Äôimportance des parcs pour le bien-√™tre psychologique des citadins. De plus, le m√™me article rappelait que les quartiers les plus d√©favoris√©s avaient un acc√®s plus restreint aux espaces verts comme l‚Äôexpliquait la mairesse, Christine Black, de l‚Äôarrondissement{{'\xa0'}}Montr√©al-Nord : ¬´{{'\xa0'}}c‚Äôest d‚Äôailleurs une des in√©galit√©s mises en lumi√®re par l‚Äô√©closion du nombre de cas de COVID-19 √† Montr√©al-Nord. C‚Äôest dans cet arrondissement qu‚Äôon trouve l‚Äôindice parc le plus bas √† Montr√©al.{{'\xa0'}}¬ª <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/ff83c3e0-b983-42ae-9d85-f2de0fbcd024__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>√Ä propos des transports en commun, <i>Radio-Canada </i>divulguait, le 14{{'\xa0'}}avril 2020, que le plan de relance √©conomique orchestr√© par la Ville de Montr√©al proposerait un d√©veloppement des services de transport en commun <a class="reference-link" target="_blank" href="https://ici.radio-canada.ca/nouvelle/1693999/causerie-ccmm-leblanc-coronavirus-covid-19-etat-urgence-bixi?fromApp=appInfoIos&amp;partageApp=appInfoiOS&amp;accesVia=partage">R√©f{{'\xa0'}}&#x1f855;</a>. Cependant, selon l'entrevue de Jean-Philippe Meloche dans <i>Le Devoir</i>, le 14{{'\xa0'}}avril 2020, les Montr√©alais vivaient au terme du confinement un traumatisme face aux transports en commun. Malheureusement, ceci pourrait encourager l‚Äôutilisation de l‚Äôautomobile et par le fait m√™me augmenter la congestion sur les routes suite au confinement <a class="reference-link" target="_blank" href="https://www.ledevoir.com/societe/576952/coronavirus-la-vie-a-deux-metres-de-distance">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Toutefois, du c√¥t√© environnemental, le journaliste, Fran√ßois Cardinal, dans <i>La Presse</i> d√©non√ßait le 16 avril 2020 que le <i>Plan de relance √©conomique du Qu√©bec</i> post-crise sanitaire n‚Äôa pas inclus le ministre de l‚ÄôEnvironnement, ni le ministre de l‚Äô√ânergie <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/9a5bd941-ce1d-429b-9ea9-45d70d929ee0__7C___0.html">R√©f{{'\xa0'}}&#x1f855;</a>. Ainsi, certains doutes sont soulev√©s face √† l‚Äôimplantation de mesures √©conomiques post-crises qui pourraient combattre les changements climatiques.{{'\xa0'}}</p>
					<p>Concernant les actions collectives, J√©r√¥me Glad de l‚ÄôOBNL <a class="reference-link" target="_blank" href="https://www.pepiniere.co/mission-et-vision"><i>la P√©pini√®re</i></a> r√©digeait un texte d‚Äôopinion dans <i>La Presse</i>, le 11{{'\xa0'}}mai 2020, qui proposait d‚Äôam√©nager de grandes terrasses dans les parcs pour permettre aux restaurants de reprendre certaines activit√©s, tout en conservant les mesures de distanciation. L‚Äôorganisme sugg√©rait aussi aux commer√ßants qui ont pignon sur rue d‚Äôutiliser √† leur avantage leur vitrine pour encourager l‚Äôachat local <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/e272d5e3-26e0-459e-b7a2-4a03fd8b6b5d__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Dans une chronique d‚ÄôAlexandre Pratt pour <i>La Presse</i>, le 1<sup>er</sup>{{'\xa0'}}mai 2020, il soulevait que la crise sanitaire semblait prouver √† court terme l‚Äôefficacit√© du t√©l√©travail. Face √† la crise √©conomique, les entreprises devront r√©viser leurs d√©penses. Malheureusement, le manque de financement pour la location d‚Äôun espace de travail dans ce secteur pourrait engendrer un d√©clin du centre-ville <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/7a745908-a14e-44a6-8aa8-2d4b8c7c4a89__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p>Au niveau du gouvernement, les quotidiens montr√©alais <i>La Presse</i> et <i>La Gazette</i> r√©v√©laient que la crise de la COVID-19 a mis √† d√©couvert la fragilit√© du syst√®me de sant√© √† Montr√©al et la difficult√© √† respecter les mesures de distanciation dans les quartiers d√©munis et denses, tels que Montr√©al-Nord (1<sup>er</sup>{{'\xa0'}}mai 2020) <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/bcfef450-1b54-452e-bd28-c12653ae7070__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a> et Mercier-Hochelaga-Maisonneuve (9{{'\xa0'}}mai 2020) <a class="reference-link" target="_blank" href="https://montrealgazette.com/news/local-news/analysis-why-is-the-covid-19-death-rate-soaring-in-hochelaga-maisonneuve">R√©f{{'\xa0'}}&#x1f855;</a>. <i>La Presse</i> pr√©sumait que l‚Äôexposition de ces probl√®mes risque de forcer le gouvernement √† investir davantage en sant√© publique, et ce dans les arrondissements les plus d√©munis <a class="reference-link" target="_blank" href="https://plus.lapresse.ca/screens/bcfef450-1b54-452e-bd28-c12653ae7070__7C___0.html?utm_medium=email&amp;utm_campaign=internal+share&amp;utm_content=screen">R√©f{{'\xa0'}}&#x1f855;</a>.</p>
					<p class="conclusion">√Ä la suite du confinement montr√©alais, divers changements peuvent √™tre attendus en fonction des individus, des actions collectives, municipales et gouvernementales. En effet, de mani√®re individuelle, il est rapport√© dans les m√©dias par les experts que le transport en commun int√©resse moins les Montr√©alais, compte tenu de la difficult√© √† respecter les mesures sanitaires dans un espace restreint. Cependant, les mesures de la Ville de Montr√©al dans le ¬´{{'\xa0'}}<i>Plan estival</i>{{'\xa0'}}¬ª favorisant une mobilit√© plus durable et abordable ax√©e sur le v√©lo et la marche √† l‚Äôensemble du territoire montr√©alais pourraient encourager la population √† adopter, √† court et √† moyen terme, individuellement ces deux modes de transport au quotidien. En ce qui a trait aux actions collectives de la part des entreprises et du milieu des affaires, la p√©riode de confinement a permis de tester l‚Äôefficacit√© du t√©l√©travail qui s‚Äôest av√©r√© pertinent pour maintenir notamment les activit√©s √©conomiques du secteur tertiaire. Dans la foul√©e du confinement, il sera int√©ressant d‚Äô√©valuer le maintien de ce mode de travail et son impact sur l‚Äôam√©nagement des centres-villes nord-am√©ricains (mobilit√©, espace de bureau, services, etc.) et l‚Äôenvironnement (ex{{'\xa0'}}: r√©duction de la production des gaz √† effet de serre, etc.). De plus, en regard de l‚Äôinformation analys√©e, il est important de soulever que l‚Äôaccessibilit√© aux parcs publics montr√©alais √©tait in√©gale et que celle-ci devrait √™tre repens√©e afin d‚Äô√™tre plus inclusive envers toutes les communaut√©s montr√©alaises pour contribuer pleinement √† leur bien-√™tre urbain dans une situation de crise sanitaire. Comme le soulignent certains experts, il appara√Æt n√©cessaire d‚Äôaugmenter les investissements de la Ville de Montr√©al en sant√© publique dans les secteurs les plus touch√©s par la COVID-19, tels que les arrondissements de Montr√©al-Nord et de Mercier-Hochelaga-Maisonneuve.</p>

